/* =================================================================================
~ Embed - maintains aspect ratio responsively - Credit: Nicolas Gallagher and SUIT CSS
================================================================================= */
.embed {
  position: relative;
  display: block;
  width: 100%;
  padding: 0;
  overflow: hidden; }
  .embed:before {
    display: block;
    content: "";
    padding-top: 100%; }
  .embed__item,
  .embed iframe,
  .embed embed,
  .embed object,
  .embed video {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0; }
  .embed--21by9:before {
    padding-top: 42.85714%; }
  .embed--16by9:before {
    padding-top: 177.77778%; }
  .embed--4by3:before {
    padding-top: 133.33333%; }

