================================================================================= */
body.locked {
  position: fixed;
  overflow: hidden;
  width: 100%; }

.overlay {
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  z-index: -99; }
  .overlay__color {
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.35);
